export default class {
    appWillStart() {
        // app启动之前，此时app已实例化，挂载到了sword上，但是还未启动
    }

    configDidLoad() {
        // 配置加载完成后，已存在sword.config()
        // 可以在这查看所有配置，也可以在这里更改或添加配置
    }

    serverWillStart() {
        // 服务启动之前，server已创建，但未启动，server已挂载到sword.app上
        // 可以在这里做一些服务启动之前要做的工作，比如使用socket.io，或使用node-red
    }

    serviceDidLoad() {
        // 服务加载完成，已存在sword.srvice()
        // 可以在这里为service添加拦截器
    }
}
